<template>
    <div class="topic">
        TOPIC DATA : {{topicName}}
    </div>
</template>

<script type = "text/javascript" >
export default {
  name: 'topic',
  props: ['topicName']
}
</script>

<style>
.topic {
    position: absolute;
    top: 0;
    left: 300px;
    width: calc(100% - 300px);
    background-color: #ffffff;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>


